import React, { Component } from 'react'
import { Row, Col, Button, Input, Table, Modal, message, Badge } from 'antd'
// import {postAction} from 'src/axios';
import styles from '../index.module.less';

export default class ChooseStrategyTargetModal extends Component {
    state = {
        strategyName: '',
        selectedRowKeys: [],
        selectedRows: [],
        dataSource: [],
        columns: [{
            title: '',
            dataIndex: 'sortIndex',
            width: "8%"
        }, {
            title: '',
            dataIndex: 'strategyName',
            width: "20%",
            render: (text) => {
                return (
                    <span title={text}>{text}</span>
                )
            }
        }, {
            title: '',
            dataIndex: 'strategyTargetCollegeName',
            render: (text) => {
                return (
                    <span title={text}>{text}</span>
                )
            }
        }, {
            title: '',
            dataIndex: 'dateTime',
            width: "20%",
            render:(text,record)=>{
                return record.startDate + '~' + record.endDate;
            }
        }, {
            title: '',
            dataIndex: 'status',
            width: "10%",
            render: (text, record)=>{
                let badge;
                if(text===''){
                    badge = <Badge status="success" />
                } else if(text===''){
                    badge = <Badge status="processing" />
                } else if(text===''){
                    badge = <Badge status="warning" />
                } else {
                    badge = <Badge status="default" />
                }
                return <span>{badge}{text}</span>;
            }
        }],
        pagination: {
            current: 1, // 
            pageSize: 10, // 
            total: 0, // 
            showTotal: (total) => `${total}`, //
            showQuickJumper: true // 
        },
    }

    componentDidMount(){
        if(this.props.selectedTarget){
            this.setState({
                selectedRowKeys:[this.props.selectedTarget.id],
                selectedRows:[this.props.selectedTarget]
            })
        }
        this.getData();
    }

    getData = () => {
        let res = {"success":true,"msg":"","obj":{"pageNum":1,"pageSize":8,"size":5,"startRow":1,"endRow":5,"total":5,"pages":1,"list":[{"id":"35d425442f564c85a756e3c23c627232","createDate":"2019-04-08","updateDate":"2019-05-21","strategyName":"2020","strategyTarget":"100342,100482","startDate":"2021-05-01","endDate":"2021-06-06","status":"","strategyTargetCollegeName":"","pushType":""},{"id":"413dbcc446354a23adf69114e5934f49","createDate":"2019-06-26","updateDate":"2019-06-26","strategyName":"","strategyTarget":"100225","startDate":"2019-07-03","endDate":"2019-07-13","status":"","strategyTargetCollegeName":"","pushType":""},{"id":"74d14b5bed3548f5900461416615d3f5","createDate":"2019-04-08","updateDate":"2019-08-02","strategyName":"20192021","strategyTarget":"100394","startDate":"2021-06-01","endDate":"2021-06-01","status":"","strategyTargetCollegeName":"","pushType":""},{"id":"ac925e6e0fad490291818537139677de","createDate":"2019-04-09","updateDate":"2019-05-22","strategyName":"","strategyTarget":"100295","startDate":"2019-04-11","endDate":"2019-04-26","status":"","strategyTargetCollegeName":"","pushType":""},{"id":"c73a04a104f34e7c94a9cc09857c6ed4","createDate":"2019-04-11","updateDate":"2019-05-22","strategyName":"","strategyTarget":"100193","startDate":"2019-04-12","endDate":"2019-04-19","status":"","strategyTargetCollegeName":"","pushType":""}],"prePage":0,"nextPage":0,"isFirstPage":true,"isLastPage":true,"hasPreviousPage":false,"hasNextPage":false,"navigatePages":8,"navigatepageNums":[1],"navigateFirstPage":1,"navigateLastPage":1,"lastPage":1,"firstPage":1},"errorCode":null};

        if (res.success) {
            let dataSource = res.obj.list;
            dataSource.forEach((item,index)=>{
                dataSource[index].key = item.id;
                dataSource[index].sortIndex = index + 1;
            })
            this.setState({dataSource});
        }

        // let {studentNo,name,pageNum,pageSize} = this.state;
        // let data = {
        //     studentNo,
        //     name,
        //     pageInfo:{
        //         pageNum,
        //         pageSize,
        //       }
        // }
        // postAction("/bigdata/student/getStudentInfoPager", data).then(
        //     (res) => {
        //         if (res.success) {
        //             this.setState({
        //                 total: res.obj.total,
        //                 studentLists: res.obj.list || []
        //             })
        //         } else {
        //             message.warn(res.obj);
        //         }
        //     }
        // )
    }

    onOk = ()=> {
        if(this.state.selectedRows[0]){
            this.props.onOk(this.state.selectedRows[0])
        } else {
            message.warn('')
        }
    }

    rowClick = (record) => {
        this.setState({
            selectedRowKeys: [record.key],
            selectedRows: [record]
        })
        // let data = {strategyId: record.id};
        // postAction("/bigdata/alarmHolidayWhitelist/checkName", data).then(
        //     (res) => {
        //         if (res.success) {
        //             if(res.obj){
        //                 message.error('')
        //             }else{
        //                 this.setState({
        //                     selectedRowKeys: [record.key],
        //                     selectedRows: [record]
        //                 })
        //             }
                    
        //         } else {
        //             message.warn(res.obj);
        //         }
        //     }
        // )
    }

    onSelectChange = (selectedRowKeys, selectedRows) => {
        this.setState({ selectedRowKeys, selectedRows });
    }

    handleTableChange = (pagination) => {
        this.setState({
            pagination: {
                current: pagination.current, // 
                pageSize: pagination.pageSize, // 
                total: pagination.total, // 
                showTotal: (total) => `${total}`,
                showQuickJumper: true // 
            }
        },() => {
            // this.getData();
        })
    }

    render() {
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            type: 'radio',
            columnWidth: "40px",
            onChange: this.onSelectChange,
        };

        return (
            <Modal
                title={''}
                visible={this.props.visible}
                centered
                onCancel={this.props.onCancel}
                onOk={this.onOk}
                width={1004}
            >
                <Row style={{marginBottom:20}}>
                    <Col span={9}>
                        <Input style={{width:246}} placeholder={''} value={this.state.strategyName} 
                                onChange={(e)=>this.setState({strategyName:e.target.value})}/>
                    </Col>
                    <Col span={12}>
                        <Button icon="search" type="primary" onClick={this.getData}> </Button>
                    </Col>
                </Row>
                <Table 
                    className={styles.clickRow}
                    dataSource={this.state.dataSource} 
                    rowSelection={rowSelection}
                    columns={this.state.columns}
                    size="middle"
                    pagination={this.state.pagination}
                    bordered
                    onChange={this.handleTableChange}
                    onRow={(record, index) => {
                        return {
                            onClick: () => this.rowClick(record, index),// 
                        };
                    }} />
            </Modal>
        )
    }
}